.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-right: 0.5rem;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}
.table-icon {
    display: flex;
    align-items: center;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button-line {
    width: 30px;
    height: 2px;
    background: var(--primary-text-color);
}