.box input[type=submit] {
  background: transparent;
  border: none;
  outline: none;
  background: #03a9f4;
  color: #fff;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
}

.box input[type=submit]:active {
    transform: scale(0.98);
    /* Scaling button to 0.98 to its original size */
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
    /* Lowering the shadow */
}

#logged-in {
  width: 100%;
  height: auto;
  text-align: center;
  margin: auto;
  position: absolute;
  top: 50px;
  display: none;
}

.login-true {
  opacity: 0;
}


.input-fields {
    margin: 18px 0px;
    width: 200px;
    display: block;
    border: none;
    padding: 10px 0;
    border-bottom: solid 1px #03a9f4;
    -webkit-transition: all 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
    transition: all 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 96%, #03a9f4 4%);
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, #03a9f4 4%);
    background-position: -200px 0;
    background-size: 200px 100%;
    background-repeat: no-repeat;
    color: #0e6252;
  }
  
  .input-fields:focus, .input-fields:valid {
   box-shadow: none;
   outline: none;
   background-position: 0 0;
   
  }
  
  
  .input-fields::-webkit-input-placeholder {
   -webkit-transition: all 0.3s ease-in-out;
   transition: all 0.3s ease-in-out;
  }
  
  .input-fields:focus::-webkit-input-placeholder, .input-fields:valid::-webkit-input-placeholder {
   color: #03a9f4;
   -webkit-transform: translateY(-20px);
   transform: translateY(-20px);
   visibility: visible !important;
  font-size: 15px;
  z-index: 1000000000000;
  }


