.group-btns {
    display: flex;
    justify-content: space-evenly;
    margin-top: 0.3rem;
    height: 39px;
}
.card-img {
    border-radius:100%;
    width: 10px;
}
.card-img-top {
    height: 180px;
    border-radius: 100%;
}
.spacer {
    display: flex;
    flex: 1;
}
.disable-btn {
    pointer-events: none;
    opacity: 0.5;
    background: #ffffff;
}
.client-card {
    margin-top: 10px;
    border: 1px solid #dee2e6;
    margin-bottom: 10px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow-x: hidden;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background: var(--heading-color-2);
    box-shadow: var(--box-shadow);
}
.ticketId {
    color: var(--primary-text-color);
    display: inline;
    position: relative;
    top: 4px;
    left: 4px;
    font-size: small;
    font-family: Helvetica, sans-serif;
    font-weight: bold;
    text-align: center;
    text-shadow: rgb(0 0 0 / 30%) 5px 5px 5px;
}
.rtc-btns {
    position: sticky;
    top: 0;
    background: var(--heading-color-2);
    box-shadow: var(--box-shadow);
    padding-bottom: 9px;
}
.calling-options {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
}

.no-preview-text {
    margin-top: 60px;
    text-align: center;
    color: var(--primary-text-color);
}
.no-preview-img {
    display: flex;
   justify-content: center;
   align-items: center;
}
.call-optns {
    text-align: center;
}
.show-modal {
    display: flex;
}
.hide-modal {
    display: none !important;
}
.modal-backdrop.show {
    opacity: .5;
    display: none;
}
.side-drawer-props {
    position: fixed;
    top: 0;
    left: 0;
    width: 60%;
    max-width: 400px;
    height: 100vh;
    z-index: 200;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}

.side-drawer-props.open {
    transform: translateX(0);
}