

#conversation-list {
    grid-area: conversation-list;
    background: #ffffff;
    overflow-y: auto;
    /* transform: translate(0px, -45px); */
    max-height: 81vh;
}

.activity-color4 {
  border-right: 4px solid rgb(0, 255, 42);
}
.activity-color2 {
  border-right: 4px solid rgb(255, 196, 0);
}
.activity-color3 {
  border-right: 4px solid #17a2b8;
}
.activity-color1 {
  border-right: 4px solid #ff1a30;
}
.activity-color5 {
  border-right: none;
}



.status1 {
  background-color: #ff1a30;
}
.status2 {
  background-color: #ff8c11;
}
.status3 {
  background-color: #f4d459;
}
.status4 {
  background-color: #979494;
}
.status5 {
  background-color: rgb(0, 0, 0);
}

.selected {
  background: var(--primary-background-4) !important;
  border-left: 4px solid #3fa3ff !important;
  color: var(--primary-text-color) !important;
  cursor: pointer !important;
  box-shadow: var(--box-shadow);
}
.selected >.title-text, .selected> .created-date {
  color: var(--primary-text-color);
}

.online {
  background-color: rgb(0, 255, 42);
}

.offline {
  background-color: rgb(235, 0, 0);
}

.busy {
  background-color: #ffc107;
}


.conversation {
    display: grid;
    grid-template-columns: 40px 1fr max-content;
    grid-gap: 2px;
    color: var(--secondary-text-color);
    background: var(--chat-color-1);
    font-size: 1rem;
    border-bottom: 2px solid var(--secondary-text-color);
    padding: 10px 10px 10px 10px;
    border-radius: 5px;
}



