.priority-indicator {
    height: 8px;
    width: 8px;
    border-radius: 50%;
    display: inline-block;
    transform: translate(-5px,-10px);
  }

.status-indicator {
    height: 8px;
    width: 8px;
    border-radius: 50%;
    display:inline-block;
    transform: translate(-5px,-2px);
}


.conversation.active,
.conversation:hover{
  background: var(--selected-bg);
    border-left: 4px solid #3fa3ff;
    color: var(--primary-text-color);
    /* transform: scale(1.2); */
    box-shadow: 0 9px 47px 11px rgba(51, 51, 51, 0.18);
  }

.conversation:hover,.conversation:hover > .created-date{
  color: var(--primary-text-color);
}
.conversation:hover,.conversation:hover > .title-text{
  color: var(--primary-text-color);
}

