.conversation:hover {
    cursor: pointer;
}


.conversation > img {
    grid-row: span 2;
    height: 40px;
    width: 40px;
    border-radius:100%;
    box-shadow: var(--box-shadow);

}

.selectedConversation > img {
    grid-row: span 2;
    height: 40px;
    width: 40px;
    border-radius:100%;
    box-shadow: var(--box-shadow);
}


.title-text {
    font-weight: 640;
    color: var(--secondary-text-color);
    padding-left: 5px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
}
.title-text-unread {
    font-weight: 700;
    color: #3c4858;
    padding-left: 5px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
}


.created-date {
    color: var(--secondary-text-color);
    white-space: nowrap;
    font-size: 0.6rem;
}
.new-created-date {
    font-weight: 700;
    color: #3c4858;
    white-space: nowrap;
    font-size: 0.6rem;
}

.conversation-message {
    grid-column: span 2;
    padding-left: 5px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
}
.new-conversation-message {
    animation:myfirst 10s 1;
    font-weight: 700;
    grid-column: span 2;
    padding-left: 5px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
}

.logout-dropdown {
  position: absolute;
  left: 133px;
  background-color: #66D;
  box-shadow: 0px 0px 3px 3px #66D;
  outline: none;
  opacity: 0;
  z-index: -1;
  max-height: 0;
  /* transition: opacity 0.1s, z-index 0.1s, max-height: 5s; */
}

.dropdown-container:focus {
  outline: none;
}

.dropdown-container:focus .logout-dropdown {
  opacity: 1;
  z-index: 100;
  max-height: 100vh;
  /* transition: opacity 0.2s, z-index 0.2s, max-height: 0.2s; */
}

.three-dots:after {
    /* display: contents; */
    cursor: pointer;
    color: #3fa3ff;
    content: '\2807';
    font-size: 20px;
    padding: 0 5px;
  }
.agent-last-seen {
    grid-column: span 2;
    padding-left: 45px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
    margin-top: -21px;
}




@keyframes myfirst
{
    0%      {color:#dee2e6;}
    25%     {color:#3fa3ff;}
    75%     {color:#3fa3ff;}
    100%    {color:#dee2e6;}
}


