.add-details {
    overflow-y: auto;
    width: auto !important;
    height: 100vh;
}
.card-img {
    border-radius:100%;
    width: 10px;
}
.card-img-top {
    height: 180px;
    border-radius: 100%;
}
.bot-chat {
    margin-top: 10px;
    border: 1px solid #dee2e6;
    margin-bottom: 10px;
    /* white-space: nowrap; */
    /* text-overflow: ellipsis; */
    /* overflow-x: hidden; */
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.ticketId {
    display: inline;
    position: relative;
    top: 2px;
    left: 4px;
    font-size: small;
}
.rtc-btns {
    position: sticky;
    top: 0;
}
.calling-options {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
}
.no-preview-text {
    margin-top: 60px;
    text-align: center;
}
.no-preview-img {
    display: flex;
   justify-content: center;
   align-items: center;
}
.call-optns {
    text-align: center;
}
.show-modal {
    display: flex;
}
.hide-modal {
    display: none !important;
}
.modal-backdrop.show {
    opacity: .5;
    display: none;
}
.sidebar-prop-drawer {
    position: fixed;
    top: 0;
    left: 0;
    width: 77%;
    max-width: 400px;
    height: 100vh;
    z-index: 200;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
  }
  
  .sidebar-prop-drawer.open {
    transform: translateX(0);
}
.desktop-view {
    width: 100%;
}