.widget-container {
    /* display: flex;
    justify-content: flex-end; */
    top: 0;
    right: 0;
    height: 0;
    position: absolute;
    cursor: pointer;
    --color: #ef6eae;
    --hover: #ef8f6e;
}

.chat-icon {
    width: 60px;
    box-shadow: var(--box-shadow-2);
    height: 60px;
    border-radius: 50%;
    padding: 8px;
    margin: 10px 10px;
    color: honeydew;
    background-color: var(--navbar-color);
}

.bot-chat-box {
    height: 67vh;
}

.call-timer-container {
    top: 0;
    right: 0;
    position: absolute;
    height: 25px;
    width: 30%;
    border-radius: 12px;
    box-shadow: var(--box-shadow-2);
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    cursor: pointer;
}

.in-call-status {
    height: 15px;
    width: 15px;
    background-color: rgb(223, 35, 35);
    border-radius: 50%;
    display: inline-block;
    margin-top: 3px;
    box-shadow: var(--box-shadow-2);
}

.text-style {
    /* color: var(--light); */
    margin: 0 !important;
    color: var(--primary-text-color);
    display: inline;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-family: Helvetica, sans-serif;
    font-weight: bold;
    text-align: center;
    text-shadow: rgb(0 0 0 / 30%) 5px 5px 5px;
    overflow: hidden;
}

.call-timer-container:hover,
.call-timer-container:focus {
    box-shadow: inset 0 0 0 2em var(--navbar-color);
}

.chat-icon:hover,
.chat-icon:focus {
    animation: chat-icon 15s;
    box-shadow: 0 0 0 2em transparent;
}

@keyframes widget-container {
    0% {
        box-shadow: 0 0 0 0 #ef8f6e;
    }
}