.no-calls-list {
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--primary-background-2);
  height: 90vh;
}


.layout {
  display: grid;
  grid-template-columns: 26% 52% 22%;
  /* grid-template-columns:  100%; */
  grid-gap: 0em;
}




@media only screen and (max-width: 769px) {
  .layout {
    display: grid;
    /* grid-template-columns: 26% 52% 22%; */
    grid-template-columns: 100%;
    grid-gap: 0em;
  }
}



