.side-call-drawer {
  /* width: 100%; */
  height: 100%;
  transform: translate(0, 0);
  position: fixed;
  top: 0;
  left: 0;
  width: 77%;
  max-width: 500px;
  height: 100vh;
  z-index: 200;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.side-call-drawer.open {
  transform: translateX(0);
}


