.before-chat-box {
    display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      height: 100%;
      background: var(--secondary-background);
  }
  .disp-text {
    font-family: Helvetica, sans-serif;
    font-weight: bold; 
    text-align: center;
    text-shadow: rgb(0 0 0 / 30%) 5px 5px 5px;
    font-size: x-large;
  }
  .no-chats {
    text-align: center;
    padding-top: 0px;
    padding-bottom: 0px;
    font-family: Helvetica, sans-serif;
    font-weight: bold;
    font-size: x-large;
    text-shadow: rgb(0 0 0 / 30%) 5px 5px 5px;
    color: white;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, 85%);
}

.no-calls-list > h6 {
  font-family: Helvetica, sans-serif;
  font-weight: bold;
  text-align: center;
  text-shadow: rgb(0 0 0 / 30%) 5px 5px 5px;
  font-size: x-large;
  color: #fff;
}
.no-calls-list {
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--primary-background-2);
  height: 90vh;
}
.no-incomming {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: var(--chat-color-1);
}
.no-incomming > h4 {
  font-family: Helvetica, sans-serif;
  font-weight: bold;
  text-align: center;
  text-shadow: rgb(0 0 0 / 30%) 5px 5px 5px;
  font-size: x-large;
  color: black;
}