.ImageType1 {
    width: 100%;
    display: flex;
    justify-content: center;
}


.MultiCellType1 {
    padding-left: 4px;
    flex: 0px;
    border: 1px solid #ddd;
    justify-content: center;
    align-items: center;
    min-width: 1%;
}

.CardType1 {
    flex-direction: column;
    padding: 2px;
    height: auto;
    align-items: center;
    display: flex;
}

.OptionButton {
    color: #3c4858;
    line-height: 19.9995px;
    display: block;
    border: none;
    font-stretch: 100%;
    width: 80%;
    max-width: 200px;
    min-width: 100px;
    margin: 4px;
    word-break: break-word;
    padding: 2px;
    /*    background-color: rgb(2, 136, 209);*/
    border-radius: 50px;
    cursor: pointer;
    touch-action: manipulation;
    -webkit-font-smoothing: antialiased;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    text-decoration-thickness: auto;
}

.OptionButtonFB {
    color: #3c4858;
