.VertCardsList {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 260px;
}

.gallery {
    position: relative;
    width: 100%;
    overflow-x: none;
}

.gallery_scroller {
    scroll-snap-type: x mandatory;
    scroll-padding: auto;
    overflow-x: auto;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    -webkit-overflow-scrolling: touch;
    padding-left: 4px;
    /* margin-right: -20rem; */
    width: 40vw;
    min-width: 100%;
    max-width: 50vw;
}

.gallery_scroller .gallery_scroller_item {
    /* snap align center  */
    scroll-snap-align: center;
    min-width: 240px;
}

.gallery_scroller .gallery_scroller_item>p {
    margin: 0.3rem !important;
}

.gallery_scroller .CardType1 {
    flex-direction: column;
    border: 1px solid #ddd;
    padding: 2px;
    max-width: 250px;
    min-width: 150px;
    height: auto;
    align-items: center;
    display: flex;
    background-color: #e6ecf1;
}

.VertCardsList .CardType1 {
