:root {
  --standard-blue: #3fa3ff;
  --standard-blue-1: #03a9f4;
  --standard-blue-2: #dbeff9;
  --standard-blue-3: #5bc7f8;
  --navbar-color: #272727;
  --selected-bg: linear-gradient(to bottom, #707586c7, #c3d6dc);
  --navbar-color-1: linear-gradient(to right, #434343 0%, black 100%);
  --primary-background: #b9c2cec9;
  --primary-background-1: rgb(26 25 25);
  --primary-background-2:linear-gradient(to bottom, #46515b, #d6e3f1);
  --primary-background-3: linear-gradient(to top, #000000, #434343);
  --primary-background-4: linear-gradient(to bottom, #283048, #859398);
  --primary-background-5: linear-gradient(to bottom, #485563, #29323c);
  --secondary-background: linear-gradient(to bottom, #8e9eab, #eef2f3);
  --heading-color: rgb(52 58 64);
  --heading-color-1:linear-gradient(to top, #232526, #414345);
  --heading-color-2:rgb(126 131 137);
  --heading-color-3:rgb(91 96 101);
  --chatlist-color: #393e44;
  --chat-color: #6f7378;
  --chat-color-1:linear-gradient(to bottom, #8e9eab, #eef2f3);
  --primary-text-color: #ffffff;
  --primary-text-color-1: #3fa3ff;
  --secondary-text-color: rgb(41, 39, 39);
  --secondary-text-color: #3c4858;
  --shadow-color: linear-gradient(360deg, rgba(27,27,30,1) 84%, rgba(7,7,8,0.8155856092436975) 92%, rgba(120,127,131,0.8660057773109244) 98%);
  --my-message-color: rgb(31 147 255 / 80%);
  --sent-message-color: rgb(13 12 25 / 80%);
  --box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
  --box-shadow-1: rgba(0, 0, 0, 0) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.08) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
  --box-shadow-2: 0px -1px 20px 2px black, 0 5px 3px -3px black;
  --box-shadow-3:rgb(134 199 255) 5px 4px 18px 0px inset, rgb(255 255 255 / 50%) -3px -3px 6px 1px inset;
  --box-shadow-4:rgb(204 219 232) 3px 3px 6px 0px inset, rgb(255 255 255 / 50%) -3px -3px 6px 1px inset;
  --box-shadow-5: rgb(0 0 0 / 25%) 0px 14px 28px, rgb(0 0 0 / 22%) 0px 10px 10px;
}
