.status {
    display: inline;
    top: 57%;
    margin-left: 95%;
    text-overflow: ellipsis;
}
.dot {
    height: 15px;
    width: 15px;
    background-color: rgb(20, 199, 65);
    border-radius: 50%;
    display: inline-block;
    margin-right: inherit;
    box-shadow: var(--box-shadow-2);
  }
/* .dropdown-menu {
    top: 56% !important
} */
.header-spacer {
    display: flex;
    flex: 1;
}
.navbar-dark .navbar-brand {
    color: #fff;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow-x: hidden;

}
.nav-bar-1 {
    background: var(--heading-color-2);
    box-shadow: var(--box-shadow);
}
.dropdown-btn {
    box-shadow: var(--box-shadow-5) !important;
    background: var(--standard-blue) !important;
}
.dropdown-btn:active {
    transform: translateY(4px);
}
.dropdown-menu {
    background: var(--heading-color-1) !important;
    box-shadow: var(--box-shadow-5) !important;
}
.dropdown-item {
    color: #3fa3ff !important;
}
.agent-username {
    color: var(--primary-text-color);
    display: inline;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-family: Helvetica, sans-serif;
    font-weight: bold;
    text-align: center;
    text-shadow: rgb(0 0 0 / 30%) 5px 5px 5px;
    overflow-x: hidden
}
.agent-nav-bar {
    box-shadow: 5px -4px 47px 12px rgb(0 0 0 / 26%);
    border-right: 1px solid #dee2e6;
    height: 52px;

}
