.nav-text-change {
    color: #3c4858;
    display: block;
    padding: .5rem 1rem;
}
.power-btn {
    color: red !important;
    display: block !important;
    padding: .5rem 1rem !important;
}
.side-drawer {
    position: fixed;
    top: 0;
    left: 0;
    width: 60%;
    max-width: 400px;
    height: 100vh;
    z-index: 200;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}

.side-drawer.open {
    transform: translateX(0);
}



.nav-text-change.active,
.nav-text-change:hover {
    color: #3fa3ff  ;
    
}
.power-btn.active,
.power-btn:hover {
    color: red !important;
    transform: scale(1);
}

.listPane {
    height: calc(100vh - 93px);
    overflow-y: auto;
}
.agent-list-pane {
    height: 50vh;
    overflow-y: auto;
}
.agent-chat-list-pane {
    height: 28vh;
    overflow-y: auto;
}
