.chat-message-list {
    /* display:flex;
    flex-direction:column; */
    padding: 0 10px;
    /* height: calc(100vh - 170px); */

    overflow-y: auto;
    /* margin-top: 40px; */
    /* overflow-x: hidden; */
}

.chat-message-list-ios {
    /* margin-top: 16px; */
    padding: 0 10px;
    /* height: calc(100vh - 190px); */

    overflow-y: auto;
}

.loading-container {
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.bot-message-section {
    display: flex;
    justify-content: flex-end
}

.wraper {
    position: relative;
    /* height: 100vh; */
    display: flex;
    flex-direction: column;
}

.wraper-mobile {
    position: relative;
    /* height: 85vh; */
    display: flex;
    flex-direction: column;
}

.before-chat-box {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    /* flex-grow: 1; */
}

.before-chat-img {
    width: 222px;
    /* margin: 24% 33% -23% 33%; */
}


/* .before-box {
    text-align: center;
    margin-top: 30%;
  } */
@media only screen and (min-width: 769px) {
    #toggle-button {
        display: none;
    }

    .chat-message-list {
        /* display:flex;
        flex-direction:column; */
        padding: 0 20px;
        /* height: calc(100% - 60px); */
        /* height: calc(100vh - 114px); */
        /* overflow-y: scroll; */
    }

}

.message-row {
    display: grid;
    grid-template-columns: 100%;
    margin-bottom: 20px;
}

.from-msg-avatar {
    display: flex;
    justify-self: end;
}

.message-content {
    display: grid;
}

.you-message .message-content {
    justify-content: end;
}

.manager-message .message-content {
    justify-content: end;
}

.other-message .message-content {
    justify-items: start;
    /* max-width: 75%;
    word-break: break-word; */
}

.other-message .message-content {
    grid: template columns 48px 1fr;
    grid-column-gap: 15px;

}

.message-row img {
    border-radius: 100%;
    grid-row: span 2;
    height: 40px;
    width: 40px;
}

.message-time {
    font-size: 0.7rem;
    color: var(--secondary-text-color);
    text-align: end;
    margin-block-end: -23px;
    transform: translate(5px, -3px);
    z-index: 1 !important;
}

.message-time-sender {
    font-size: 0.7rem;
    color: var(--secondary-text-color);
    text-align: start;
    margin-block-end: -23px;
    transform: translate(3px, -4px);
    z-index: 1 !important;
}

.message-time-admin {
    font-size: 0.7rem;
    color: var(--secondary-text-color);
    text-align: center;
    margin-block-end: -23px;
    z-index: 1 !important;
}

.message-text {
    padding: 9px 14px;
    font-size: 0.8rem;
    margin-bottom: 5px;
}

.you-message .message-text {
    background: var(--my-message-color);
    /* max-width: 75%; */
    /* margin-left: 15%; */
    width: fit-content;
    color: black;
    word-break: break-word;
    /* border: 1px solid #e9ecef; */
    border-radius: 9px 9px 0 9px;
    box-shadow: var(--box-shadow-1);
}

.manager-message .message-text {
    background: var(--standard-blue-3);
    /* max-width: 75%; */
    /* margin-left: 15%; */
    width: fit-content;
    color: black;
    word-break: break-word;
    /* border: 1px solid #e9ecef; */
    border-radius: 9px 9px 0 9px;
    box-shadow: var(--box-shadow-1);
}

.bot-message .message-text {
    background: #d9dddf;
    /* max-width: 75%; */
    /* margin-left: 15%; */
    width: fit-content;
    color: var(--secondary-text-color);
    word-break: break-word;
    /* border: 1px solid #e9ecef; */
    border-radius: 9px 9px 0 9px;
    box-shadow: var(--box-shadow-5);
    margin-left: 4.3rem;
}

.admin-message .message-text {
    background: #83bbd6eb;
    /* max-width: 75%; */
    /* margin-left: 30%; */
    width: fit-content;
    color: #3c4858;
    text-align: center;
    word-break: break-word;
    /* border: 1px solid #e9ecef; */
    /* box-shadow: var(--box-shadow-1); */
    border-radius: 9px 9px 9px 9px;
    margin-top: 0.3rem;
}

.resolve-btn {
    /* background: var(--standard-blue); */
    box-shadow: var(--box-shadow-5);
}

.other-message .message-text {
    background: var(--sent-message-color);
    margin-right: 15%;
    width: fit-content;
    color: var(--primary-text-color);
    word-break: break-word;
    /* border: 1px solid #ffffff; */
    box-shadow: var(--box-shadow);
    border-radius: 9px 9px 9px 0;
    margin-top: 0.3rem;
    /* min-width: inherit; */
}

.send-messages {
    width: 91%;
    /* border-radius: 10px; */
    outline: none;
    display: inline-block;
    height: 80%;
    margin-left: 7px;
    margin-top: 11px;
    display: inline-block !important;
    width: 97% !important;
    /* box-shadow: var(--box-shadow-3); */
}



.send-btn {
    /* margin-bottom: 6px; */
    height: 37px;
    margin: 11px 10px 0 0;
    box-shadow: var(--box-shadow-5);
    /* border-radius: 10px; */
}

.nav-bar {
    /* box-shadow: 3px 6px 47px -5px rgb(0 0 0 / 26%); */
    display: flex !important;
    justify-content: space-between !important;
    background: var(--heading-color-2);
    box-shadow: var(--box-shadow);
    height: 55px;

    /* position: sticky; */
    /* top: 0px; */
    /* position: '-webkit-sticky', */
}

.chat-container {
    overflow-y: auto;
    flex: 1;
    padding: 0 20px;
    background: var(--secondary-background);
}

